import React, { useState ,useEffect} from "react";
import "./TestCasecheckbox.scss";
function TestCaseCheckbox({ onSelectedTestCasesChange }) {
  const testCases = [
    "Test Case 1",
    "Test Case 2",
    "Test Case 3",
    "Test Case 4",
    "Test Case 6",
    "Test Case 7",
    "Test Case 8",
    "Test Case 9",
    "Test Case 10",
    "Test Case 11",
    "Test Case 12",
  ];
  const colors = [
    "#FF5733", // Vibrant Red
    "#33FF57", // Vibrant Green
    "#3357FF", // Vibrant Blue
    "#FF33A1", // Vibrant Pink
    "#A133FF", // Vibrant Purple
    "#33FFF5", // Vibrant Cyan
    "#FFAA33", // Vibrant Orange
    "#33FFAA", // Vibrant Mint
    "#FF3333", // Bright Red
    "#33FF33", // Bright Green
    "#3333FF", // Bright Blue
    "#FF5733"  // Vibrant Red again (as a fallback if more colors are needed)
  ];

  const testCasesWithColors = testCases.map((testCase, index) => ({
    testCase,
    color: colors[index % colors.length]
  }));
  
  console.log(testCasesWithColors)
  
  const [checkedState, setCheckedState] = useState(
    new Array(testCasesWithColors.length).fill(false)
  );

  const handleOnChange = (position) => {
    const updatedCheckedState = checkedState.map((item, index) =>
      index === position ? !item : item
    );
     console.log(updatedCheckedState);
    setCheckedState(updatedCheckedState);
  };

  useEffect(() => {
    const selectedTestCases = testCasesWithColors.filter((_, index) => checkedState[index]);
    onSelectedTestCasesChange(selectedTestCases);
  }, [checkedState]);
  return (
    <div className="testcase-box">
      <h4>Test Cases</h4>
      <div className="box-list">
        {testCasesWithColors.map((testCase, index) => {
          
          return (
            
            <div className="check-box" key={index}>
              <input
                type="checkbox"
                id={`checkbox-${index}`}
                name={testCase}
                value={testCase}
                checked={checkedState[index]}
                onChange={() => handleOnChange(index)}
              />
              <label htmlFor={`checkbox-${index}`}>{testCase.testCase} {checkedState[index] && <span style={{ backgroundColor: `${testCase.color}` }}></span>}  </label>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default TestCaseCheckbox;
